.footer{
  margin-top: 100px;
  padding-bottom: 20px;
  background-color: $section-grey;
  &__logo__link{
    @include md-block(){
      padding-right: 60px;
    }
  }
  .footer__adress__primary{
    margin-right: 0;
    margin-top: 20px;
  }
  padding-top: 60px;
  &__adress {
    p{margin: 0;}
  }
  &__wrap{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include md-block(){
      flex-direction: column;
      align-items: center;
      text-align: center;
    };
    ul li{
      padding-bottom: 15px;
    }
    }



  &__adress{
    flex-direction: column;
    text-align: right;
      @include md-block(){
        text-align: center;
        padding-top: 40px;
      }
    &__primary{
      order: 2;
      }
    }
    &__primary, &__secondary{
      margin-right: 0;
      p{
        a{
          color: $grey-primary;
        }
      }
    }
    &__online {
        margin-top: 30px;
        li {@include md-block(){
          margin-top: 10px;
        }}
      li a {
       color: $orange;
       border-bottom:  2px solid $orange;
       text-decoration: none;
    }
  }
    &__menu, &__contingent{
      @include md-block(){
        display: none;
      }
      a{
      color: $grey-primary;
      text-decoration: none;
      border-bottom: 2px solid $info;
    }
  }
  }
.footer-soical{
  order: 3;
  justify-content: flex-end;
  margin-top: 20px;
  @include md-block(){
    justify-content: center;
  }
}
.politic-wrap{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  @include md-block(){
    justify-content: center;
    padding-top: 50px;
    flex-direction: column;
  }
}
.politic{
  order: 1;
  text-align: right;
  @include md-block(){
    text-align: center;
    order: 0;
  }
  &__item{
    display: block;
    margin-top: 10px;
    color: $grey-primary;
  }
}
.copyright{
  align-self: flex-end;
  color: $info;
    @include md-block(){
      padding-top: 30px;
      align-self: center;
    }
}
