.is-active{
  display: block;
}
.hamburger{
  padding-top: 15px;
  padding-bottom: 15px;
  display: none;
  width: 24px;
  cursor: pointer;
  span{
    background: $white;
    display: block;
    height: 2px;
    margin-bottom: 4px;
  }
  @include md-block(){
    display: block;
  }
}
.phone-mobile{
  display: none;
  color: $white;
  text-decoration: none;
  @include md-block(){
    display: block;
  }
}
.header{
  &__menu{
    background-color: $grey-primary;
  }
}
.navbar{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  &-left{
    position: relative;
  }
  &-right{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-wrap: wrap;
    align-self: center;
  }

}
.user-link{
  color: $grey-search;
  align-self: center;
  border-bottom: 3px solid $grey-secondary;
  margin-right: 30px;
  text-decoration: none;
  @include md-block(){
    color: $white;
  }
}
.input__search{
  background: url(../img/search.png) 10px center no-repeat;
  position: relative;
  background-color: $grey-secondary;
  border: 0;
  box-sizing: border-box;
  width: 243px;
  padding: 15px 0;
  padding-left: 35px;
  outline: none;
  color: $grey-search;
  text-align: center;
  &::placeholder{
    color: $grey-search;
  }
  @include md-block(){
    display: none;
  }
}
.menu{

  & li{
    position: relative;
    & a{
    &:hover{
      box-shadow: 1px 5px 10px -5px #000;
    }
  }
    & ul{
      min-width: 150px;
      background-color: $grey-primary;
      position: absolute;
      display: none;
      z-index: 1000;
      & li{
        margin: 0;
        border: 1px solid #ccc;
        & a{
          display: block;
           padding: 10px;
           text-decoration: none;
           transition: all 0.3s ease;
           &:hover{
             background-color: $orange;
             transition: all 0.3s ease;
             box-shadow: 1px 5px 10px -5px #000;
           }
        }
        & ul{
          position: absolute;
          right: -150px;
          top: 0;
          z-index: 1000;
        }
      }
    }
  }
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  list-style: none;
  margin: 0;
  color: $white;
  @include md-block(){
    z-index: 1000;
    width: 300px;
    background-color: $grey-primary;
    position: absolute;
    left: -50px;
    display: none;
    flex-direction: column;
    text-align: center;
    li+li{
      margin-left: 0;
      margin-top: 30px;
    }
    li:last-child{
      margin-bottom: 30px;
    }
  }
  li+li{
      margin-left: 30px;
  }
  li a{
    text-decoration: none;
    color: $white;
  }
}
.menu li:hover > ul{
 display: block;
 @include md-block(){
   display: none;
 }
}
ul.menu li {
    &:hover:only-child{
      color: $orange;
  }
}
//subheader
.subheader{
  @include md-block(){
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: space-between;
  }
  &__info{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
  &__online{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    list-style: none;
    font-weight: 700;
    @include md-block(){
      display: none;
    }
    a{
      color: $orange;
    }
    & li + li{
      margin-left: 40px;
    }
  }
}
.social{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  list-style: none;
  color: $white;
  @include md-block(){
    display: none;
  }
  & li + li{
    margin-left: 10px;
  }
  & .facebook{
    background-color: $facebook;
  }
  & .instagram{
   background: $instagram;
 }
 & .youtube{
  background-color: $youtube;
}
& .vk{
  background-color: $vk;
}
  & .icon-pos{
    a{
      width: 28px;
      height: 28px;
      border-radius: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
      border: 0;
    }
  }
}
.adress{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  text-align: right;
  @include md-block(){
    display: none;
  }
  p{
    line-height: 24px;
    a{
      font-weight: bold;
      color:$black;
    }
  }
  &__primary{
    margin-right: 46px;
    font-size: 16px;
    &-tel{
      font-size: 20px;
      & strong{
        color: $info;
        font-weight: 600;
      }
      a{
        text-decoration: none;
      }
    }
  }
  &__secondary{
    &-tel{
      font-size: 20px;
      & strong{
        color: $info;
        font-weight: 600;
      }
      a{
        text-decoration: none;
      }
    }
  }
}
.subheader__nav{
  @include md-block(){
    margin-bottom: 0;
  }
  margin-bottom: 24px;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  & .parent{
    margin-left: 13px;
  }
  &__menu{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  @include md-block(){
    display: none;
  }
  & .club{
    a{
      text-transform: uppercase;
    }
  }
  ul+ul{
    margin-left: 20px;
  }
  ul{
    margin: 0;
    list-style: none;
    li a {
      text-decoration: none;
      color: $grey-primary;
      text-transform: capitalize;
    }
  }
}
}
.button--subheader{
  font-size: 14px;
  color: $white;
  background-color: $orange;
  border: 3px solid $orange;
  margin-left: 30px;
  width: 240px;
  padding: 10px 0;

}
