@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?3284338');
  src: url('../fonts/fontello.eot?3284338#iefix') format('embedded-opentype'),
       url('../fonts/fontello.woff2?3284338') format('woff2'),
       url('../fonts/fontello.woff?3284338') format('woff'),
       url('../fonts/fontello.ttf?3284338') format('truetype'),
       url('../fonts/fontello.svg?3284338#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
