//SECTION slider
.bxslider{
  margin: 0;
  padding: 0;
}
.bx-wrapper {
  box-shadow: none;
  border: 0;
  background: #fff;
}

.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
  bottom: 30px;
  @include md-block(){
    bottom: 20px;
  }

}
.bx-wrapper .bx-pager.bx-default-pager a {
  @include md-block(){
    width: 16px;
    height: 16px;
    border-radius: 8px;
  }
  @include sm-block(){
  display: none;
  }
  background: #fff;
  text-indent: -9999px;
  display: block;
  width: 20px;
  height: 20px;
  margin: 0 10px;
  outline: 0;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}
.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active,
.bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: #333333;
}
.slider__img{
  position: relative;

}
.slider__info{
  position: absolute;
  top: 10%;
  left: 13%;
  color: $white;
  text-transform: uppercase;
  &-heading{
    h1{
      font-size: 40px;
      font-weight: 500;

    }
    p{
      font-size: 60px;
      font-weight: 700;
      margin: 0;
    }
    @include md-block(){
      h1{
        font-size: 25px;
      }
      p{
        font-size: 30px;
      }
    }
  }
}
.slider__button{
  box-sizing: border-box;
  width: 315px;
  font-size: 30px;
  color: $white;
  text-decoration: none;
  border: 3px solid $white;
  border-radius: 6px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: block;
  text-align: center;
  margin-top: 20px;
  @include md-block(){
    width: 180px;
    font-size: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 6px;
    padding-bottom: 6px;

  }
  @include sm-block(){
    margin-top: 5px;
  }
}
.bx-wrapper{
  margin: 0;
}
// SECTION promo
.promo{
  margin-bottom: 57px;
  padding-top: 64px;
  background-image: url(../img/section.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  h2{
    margin: 0;
    padding: 0;
    font-size: 40px;
    line-height: 150%;
    color: $grey-primary;
    text-transform: uppercase;
    @include md-block(){
      font-size: 25px;
    }
  }
}
.promo-row{
  margin-top: 45px;
  padding-bottom: 105px;
  @include row-flex();
  justify-content: center;
  &__item{
    @include col();
    @include size(4);
    @include size-md(12);
    text-transform: uppercase;
    p{
      font-size: 20px;
      line-height: 160%;
      color: $grey-primary;
      font-weight: 600;
      margin-bottom: 0;
    }
    span{
      font-size: 40px;
      line-height: 160%;
      color: $orange;
      font-weight: 600;
    }
  }
}

//SECTION Learn-language
.learn-language{
  background-color: $white;
  h2{
    text-align: center;
    text-transform: uppercase;
    @include md-block(){
    width: 328px;
    margin: 0 auto;
    font-size: 20px;
  }
  }
}
.learn-language-promo{
  &__row{
    @include row-flex();
    justify-content: center;
    margin: 0 auto;
  }
  &__item{
    @include col();
    @include size(4);
    @include size-md(12);
    @include md-block(){
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      justify-content: center;
      margin-top: 40px;
      p{
        padding-left: 30px;
        flex-basis: 250px;
      }
    }
img{
  align-self: center;
}

  }
}

//SECTION course-selection
.course-selection{
  margin-top: 70px;
  padding-bottom: 60px;
  background-color: $grey-primary;
  text-align: center;
  color: $white;
  h2{
    font-size: 30px;
    line-height: 44px;
    @include sm-block(){
      font-size: 20px;
    }
  }
  p{
    font-size: 20px;
    line-height: 130%;
  }
  &__wrap{
    padding-top: 67px;
    @include md-block(){
      order: -2;
    }
  }
  &__buttons{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

  }
  &__button{
    text-transform: uppercase;
    color: $white;
    border: 1px solid $info;
    border-radius: 6px;
    background-color: transparent;
    padding: 8px 15px;
    cursor: pointer;
    margin-left: 10px;
    margin-top: 10px;
    outline: 0;
    font-weight: 700;
    @include sm-block(){
      flex-basis: 165px;
    }
    &:hover{
      background-color: $orange;
      border: 1px solid $orange
    }
  }
  &__page{
    margin-left: 150px;
    align-self: center;
    @include md-block(){
      margin-bottom :35px;
    }
    span{
      border-radius: 50%;
      width: 24px;
      height: 24px;
      display: block;
      line-height: 24px;
      background-color: $grey-secondary;
      margin-top: 10px;
      font-weight: 700;
      cursor: pointer;
      &:hover{
        background-color: $orange;
      }
    }
    @include md-block(){
      order: 1;
    }
    @include md-block(){
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      justify-content: center;
      margin-left: 0;
      order: -2;
      span+span{
        margin-left: 15px;
      }

    }
    @include lg-block(){
      order: 1;
    }
  }
}
.course-selection__text{
    order: -3;
    @include from-to($break_md, $break_xl){
      width: 86%;
    }
  h2{
    text-transform: uppercase;
  }
}
.buttons-wrap{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.button--course-selection{
  color: $white;
  border: 3px solid $orange;
  background-color: $orange;
  padding: 8px 25px;
  margin-top: 55px;
}
.course-selection__row{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  @include md-block(){
    flex-direction: column;
  }
}


// *****************  SECTION courses-polyglot  ***************

.courses-polyglot{
  padding-bottom: 90px;
  @include md-block(){
    padding-bottom: 0;
  }
  background-color: $section-grey;
  text-align: center;
  ul{
    margin: 0;
  }
  &__img{
    align-self: flex-end;
    @include md-block(){
      display: none;
    }
  }
  &-wrap{
    padding-top: 30px;
    padding-bottom: 50px;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: baseline;
    @include md-block(){
      flex-direction: column;
      align-items: center;
    }
  }
  padding-top: 88px;
  h2{
    font-size: 30px;
    line-height: 44px;
    text-align: center;
    text-transform: uppercase;
  }
  &__language-selection{
    &__menu{
      @include md-block(){
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
      }
    }
    ul{
      list-style: none;
      text-transform: uppercase;
      font-size: 18px;
      line-height: 1.4;
      unicode-bidi: bidi-override;
      direction: rtl;
      text-align: right;
      margin: 0;
      @include md-block(){
        text-align: center;
        direction: ltr;
      }
      li+li{
        padding-top: 18px;
      }
      li a{
        text-decoration: none;
        color: $grey-primary;
      }
    }
  }
  &__service-selection{
    text-align: left;
    padding-left: 50px;
    p{
      color: $link-menu;
      font-size: 16px;
      line-height: 24px;
      width: 415px;
      @include md-block(){
        width: auto;
      }
    }
    p + p{
      padding-top: 20px;
    }
    & .service-selection__menu{
      margin-bottom: 40px;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      list-style: none;
      font-size: 18px;
      line-height: 132.66%;
      padding: 0;
      @include md-block(){
        order: 0;
      }
      li+li{
        padding-left: 24px;
          @include md-block(){
            padding-bottom: 24px;
          }
      }
      a{
          text-decoration: none;
          color: $link-menu;
      }
    }
  }
}
.service-selection__study-directions{
  list-style: none;
  padding: 0;
  li+li{
    margin-top: 20px;
  }
  a{
    color: $orange;
    text-decoration: none;
  }
  li{
    padding-left: 41px;
  }
  li.general{
    background: url("../img/comment.svg") no-repeat 0 50%;
  }
  li.express{
    background: url("../img/running.svg") no-repeat 0 50%;
  }
  li.business{
    background: url("../img/handshake.svg") no-repeat 0 50%;
  }
  li.speaking{
    background: url("../img/comments.svg") no-repeat 0 50%;
  }
  li.travel{
    background: url("../img/hiking.svg") no-repeat 0 50%;
  }
  li.fellowship{
    background: url("../img/users.svg") no-repeat 0 50%;
  }
  li.examination{
    background: url("../img/user-graduate.svg") no-repeat 0 50%;
  }

}
.button--courses-polyglot{
  margin-top: 45px;
  color: $white;
  background-color: $orange;
  border: 3px solid $orange;
  @include md-block(){
    margin-left: 40px;
  }
}
//SECTION PROJECT
.project{
  padding-top: 74px;
  text-align: center;
  padding-bottom: 74px;
  &__h2{
    font-size: 30px;
    line-height: 44px;
    text-transform: uppercase;
    margin: 0;
    @include md-block(){
      font-size: 20px;
    }
  }
  &__items{
    @include row-flex();
    justify-content: space-evenly;
    padding-top: 60px;
    @include md-block(){
      padding-top: 30;
    }
  }
  &__item{
    @include col();
    @include size(4);
    @include size-md(12);
    color: $black;
    @include md-block(){
      margin-top: 30px;
    }
    &-h3{
      font-size: 20px;
      line-height: 29px;
    }
    &-p{
      font-size: 16px;
      line-height: 24px;
    }
  }
}
.button--project{
  background-color: transparent;
  color: $orange;
  @include md-block(){
    margin-top: 34px;
  }
}

//SECTION subscribe
.subscribe{
  padding: 65px 0;
  background-image: url('../img/subscribe.png');
  text-align: center;
  color: $grey-primary;
  @include md-block(){
    padding: 10px 0;
  }
  &__wrap{
    @include row-flex();
    justify-content: space-evenly;
    padding-top: 45px;
  }
  &__items{
    @include col();
    @include size(6);
  }
  &__h2{
    margin: 0;
    font-size: 30px;
    line-height: 44px;
    text-transform: uppercase;
    @include md-block(){
      font-size: 20px;
      padding-top: 51px;
    }
  }
  &__h3{
    margin: 0;
    font-size: 20px;
    line-height: 29px;
    text-transform: uppercase;
  }
  &__text{
    text-align: left;
    font-size: 16px;
    line-height: 125%;
    flex-basis: 500px;
  }
  &__forms{
    border-radius: 4px;
    opacity: 0.9;
    padding-top: 38px;
    width: 420px;
    box-sizing: border-box;
    background-color: $white;
    & input[type="text"],input[type="email"] {
      box-sizing: border-box;
      width: 340px;
      padding: 15px 20px;
      border-radius: 6px;
      outline: none;
      border: 1px solid;
      color: $info;
      font-size: 16px;
      &::placeholder{
        text-transform: uppercase;
      }
    }
    @include md-block(){
      width: 100%;

    }
  }
}
.subscribe__item-wrap{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: center;
  & .subscribe__item{
      margin-top: 0;
    & input[type="text"]{
      width: 160px;
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}
//form button
.button-subscribe__forms{
  border: 3px solid $orange;
  color: $white;
  background-color: $orange;
  padding: 10px 30px;
  margin: 30px 0;
}


//section new
.new{
  padding-top: 70px;
  padding-bottom: 180px;
  margin: 0 auto;
  text-align: center;
  @include md-block(){
    padding-bottom: 68px;
  }
  &__h2{
    margin: 0;
    text-align: center;
    font-size: 30px;
    line-height: 44px;
    text-transform: uppercase;
    @include md-block(){
      font-size: 20px;
    }
  }
  & .bx-wrapper{
    margin: 0 auto;
    max-width: 100% !important;
  }
  .bx-wrapper .bx-pager,
  .bx-wrapper .bx-controls-auto {
    bottom: -200px;
  }
  .bx-wrapper .bx-pager.bx-default-pager a {
    background: #333333;
  }
  .bx-wrapper .bx-pager.bx-default-pager a:hover,
  .bx-wrapper .bx-pager.bx-default-pager a.active,
  .bx-wrapper .bx-pager.bx-default-pager a:focus {
    background: $orange;
  }
  .slider__new{
    @include md-block(){
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    text-align: left;
    &__title{
      font-size: 20px;
      line-height: 140%;
      text-transform: uppercase;
      padding-bottom: 15px;
    }
    &__info{
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      color: $info;
      text-align: left;
      time{
          padding-right: 67px;
      }
    }
    p{
      font-size: 16px;
      margin-top: 25px;
    }
    &__item{
      margin-right: 40px;
    @include md-block(){
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      text-align: left;
      flex-basis: 300px;
      margin-top: 50px;
      &:nth-child(-n+3){
        display: none;
      }
      &__text{
        @include md-block(){
          width: 280px;
        }
      }
    }
  }
  }
  .icon-eye-outline{
    padding-right: 18px;
  }
}
.button--new{
  color: $orange;
  background-color: $white;
  margin-top: 80px;
  @include md-block(){
    margin-top: 50px;
  }
}


